<template>
	<view class="tabs">
		<scroll-view id="tab-bar" class="scroll-h" :scroll-x="true" :show-scrollbar="false" :scroll-into-view="scrollInto">
			<view v-for="(tab,index) in tabBars" :key="tab.id" class="uni-tab-item" :id="tab.id" :data-current="index" @click="ontabtap">
				<text class="uni-tab-item-title" :class="tabIndex==index ? 'uni-tab-item-title-active' : ''">{{tab.name}}</text>
			</view>
		</scroll-view>
		<view class="line-h"></view>
		<swiper :current="tabIndex" class="swiper-box" style="flex: 1;" :duration="300" @change="ontabchange">
			<swiper-item class="swiper-item" v-for="(tab,index1) in newsList" :key="index1">
				<form @submit="formSubmit">
					<view v-for="(item, index) in portData" :key="item.sort">
						<!-- input类型 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'input'">
							<view class="title">{{item.title}}(文本框)</view>
							<input class="uni-input" :name="item.name" :placeholder="item.placeholder" :maxlength="item.maxLength" />
						</view>
						<!-- select类型 -->
						<!-- 普通选择器 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'select' && item.mode == 'selector'">
							<view class="title">{{item.title}}(普通选择器)</view>
							<picker @change="bindPickerChange" :mode="item.mode" :value="item.index" :range="item.option" :name="item.name"
							 :data-index="index">
								<view class="uni-input">{{item.option[item.index]}}</view>
							</picker>
						</view>
						<!-- #ifndef MP-ALIPAY -->
						<!-- 多列选择器(支付宝需要用piker-view实现) -->
						<view class="uni-form-item uni-column" v-if="item.label == 'select' && item.mode == 'multiSelector'">
							<view class="title">{{item.title}}</view>
							<picker :mode="item.mode" @columnchange="bindMultiPickerColumnChange" :value="item.multiIndex" :range="item.option"
							 :name="item.name" :data-index="index">
								<view class="uni-input">{{item.option[0][item.multiIndex[0]]}}，{{item.option[1][item.multiIndex[1]]}}，{{item.option[2][item.multiIndex[2]]}}</view>
							</picker>
						</view>
						<!-- #endif -->
						<!-- #ifdef MP-ALIPAY -->
						<view class="uni-form-item uni-column" v-if="item.label == 'select' && item.mode == 'multiSelector'">
							<view class="title" @click="togglePicker">{{country}}, {{province}}, {{city}}</view>
							<picker-view class="picker-view" v-if="visible" :indicator-style="indicatorStyle" :value="item.multiIndex" :name="item.name"
							 :data-index="index" @change="bindChange">
								<picker-view-column>
									<view class="item" v-for="(item,index) in item.option[0]" :key="index">{{item}}</view>
								</picker-view-column>
								<picker-view-column>
									<view class="item" v-for="(item,index) in item.option[1]" :key="index">{{item}}</view>
								</picker-view-column>
								<picker-view-column>
									<view class="item" v-for="(item,index) in item.option[2]" :key="index">{{item}}</view>
								</picker-view-column>
							</picker-view>
						</view>
						<!-- #endif -->
						<!-- 日期选择器 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'select' && item.mode == 'date'">
							<view class="title">{{item.title}}</view>
							<picker :mode="item.mode" :value="date" :start="startDate" :end="endDate" @change="bindDateChange" :name="item.name">
								<view class="uni-input">{{date}}</view>
							</picker>
						</view>
						<!-- radio类型 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'radio'">
							<view class="title">{{item.title}}</view>
							<radio-group :name="item.name">
								<label v-for="(radioData, index) in item.option" :key="index">
									<!-- 加color为了解决小程序颜色差异 -->
									<radio color="#007aff" :value="radioData.value" /><text>{{radioData.text}}</text>
								</label>
							</radio-group>
						</view>
						<!-- checkbox类型 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'checkbox'">
							<view class="title">{{item.title}}</view>
							<checkbox-group :name="item.name">
								<label v-for="(checkboxData, index) in item.option" :key="index">
									<!-- 加color为了解决小程序颜色差异 -->
									<checkbox color="#007aff" :value="checkboxData.value" /><text>{{checkboxData.text}}</text>
								</label>
							</checkbox-group>
						</view>
						<!-- switch类型 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'switch'">
							<view class="title">{{item.title}}</view>
							<!-- 加color为了解决小程序颜色差异 -->
							<switch color="#007aff" name="switch" :checked="item.checked" />
						</view>
						<!-- slider类型 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'slider'">
							<view class="title">{{item.title}}</view>
							<!-- 加activeColor为了解决小程序颜色差异 -->
							<slider activeColor="#007aff" :min="item.option.min" :max="item.option.max" :step="item.option.step" :value="item.option.value"
							 :name="item.name" show-value></slider>
						</view>
						<!-- textarea类型 -->
						<view class="uni-form-item uni-column" v-if="item.label == 'textarea'">
							<view class="title">{{item.title}}(多文本框)</view>
							<textarea :placeholder="item.placeholder" auto-height />
							</view>
			</view>
			<view class="uni-btn-v">
				<button form-type="submit" type="primary">保存</button>
			</view>
				</form>
			</swiper-item>
		</swiper>
		<view class="line-h">
			<button @click="goResult">计算</button>
		</view>
	</view>
</template>
<script>
	import mediaItem from './news-item.nvue';

	// 缓存每页最多
	const MAX_CACHE_DATA = 100;
	// 缓存页签数量
	const MAX_CACHE_PAGE = 3;

	const newsData = {
		data0: {
			"datetime": "40分钟前",
			"article_type": 0,
			"title": "uni-app行业峰会频频亮相，开发者反响热烈!",
			"source": "DCloud",
			"comment_count": 639
		},
		data1: {
			"article_type": 3,
			"image_list": [{
				"url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg?imageView2/3/w/200/h/100/q/90",
				"width": 563,
				"height": 316
			}, {
				"url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg?imageView2/3/w/200/h/100/q/90",
				"width": 641,
				"height": 360
			}, {
				"url": "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg?imageView2/3/w/200/h/100/q/90",
				"width": 640,
				"height": 360
			}],
			"datetime": "5分钟前",
			"title": "uni-app 支持使用 npm 安装第三方包，生态更趋丰富",
			"source": "DCloud",
			"comment_count": 11
		}
	};

	export default {
		components: {
			mediaItem
		},
		data() {
			return {
				newsList: [],
				cacheTab: [],
				tabIndex: 0,
				tabBars: [{
					name: '工资计算',
					id: 'gongzi'
				}, {
					name: '税前工资反推',
					id: 'shuiqian'
				},
				{
					name: '年终奖',
					id: 'nianzhong'
				}],
				scrollInto: "",
				showTips: false,
				navigateFlag: false,
				pulling: false,
				refreshIcon: "data:image/png;base64,"
			}
		},
		onLoad() {
			setTimeout(() => {
				this.tabBars.forEach((tabBar) => {
					this.newsList.push({
						data: [],
						isLoading: false,
						refreshText: "",
						loadingText: '加载更多...'
					});
				});
				this.getList(0);
			}, 350)
		},
		methods: {
			getList(index) {
				let activeTab = this.newsList[index];
				let list = [];
				for (let i = 1; i <= 10; i++) {
					let item = Object.assign({}, newsData['data' + Math.floor(Math.random() * 5)]);
					item.id = this.newGuid();
					list.push(item);
				}
				activeTab.data = activeTab.data.concat(list);
			},
			goDetail(e) {
				if (this.navigateFlag) {
					return;
				}
				this.navigateFlag = true;
				uni.navigateTo({
					url: './detail/detail?title=' + e.title
				});
				setTimeout(() => {
					this.navigateFlag = false;
				}, 200)
			},
			close(index1, index2) {
				uni.showModal({
					content: '是否删除本条信息？',
					success: (res) => {
						if (res.confirm) {
							this.newsList[index1].data.splice(index2, 1);
						}
					}
				})
			},
			loadMore(e) {
				setTimeout(() => {
					this.getList(this.tabIndex);
				}, 500)
			},
			ontabtap(e) {
				let index = e.target.dataset.current || e.currentTarget.dataset.current;
				this.switchTab(index);
			},
			ontabchange(e) {
				let index = e.target.current || e.detail.current;
				this.switchTab(index);
			},
			switchTab(index) {
				if (this.newsList[index].data.length === 0) {
					this.getList(index);
				}

				if (this.tabIndex === index) {
					return;
				}

				// 缓存 tabId
				if (this.newsList[this.tabIndex].data.length > MAX_CACHE_DATA) {
					let isExist = this.cacheTab.indexOf(this.tabIndex);
					if (isExist < 0) {
						this.cacheTab.push(this.tabIndex);
						//console.log("cache index:: " + this.tabIndex);
					}
				}

				this.tabIndex = index;
				this.scrollInto = this.tabBars[index].id;

				// 释放 tabId
				if (this.cacheTab.length > MAX_CACHE_PAGE) {
					let cacheIndex = this.cacheTab[0];
					this.clearTabData(cacheIndex);
					this.cacheTab.splice(0, 1);
					//console.log("remove cache index:: " + cacheIndex);
				}
			},
			clearTabData(e) {
				this.newsList[e].data.length = 0;
				this.newsList[e].loadingText = "加载更多...";
			},
			refreshData() {},
			onrefresh(e) {
				var tab = this.newsList[this.tabIndex];
				if (!tab.refreshFlag) {
					return;
				}
				tab.refreshing = true;
				tab.refreshText = "正在刷新...";

				setTimeout(() => {
					this.refreshData();
					this.pulling = true;
					tab.refreshing = false;
					tab.refreshFlag = false;
					tab.refreshText = "已刷新";
					setTimeout(() => { // TODO fix ios和Android 动画时间相反问题
						this.pulling = false;
					}, 500);
				}, 2000);
			},
			onpullingdown(e) {
				var tab = this.newsList[this.tabIndex];
				if (tab.refreshing || this.pulling) {
					return;
				}
				if (Math.abs(e.pullingDistance) > Math.abs(e.viewHeight)) {
					tab.refreshFlag = true;
					tab.refreshText = "释放立即刷新";
				} else {
					tab.refreshFlag = false;
					tab.refreshText = "下拉可以刷新";
				}
			},
			newGuid() {
				let s4 = function() {
					return (65536 * (1 + Math.random()) | 0).toString(16).substring(1);
				}
				return (s4() + s4() + "-" + s4() + "-4" + s4().substr(0, 3) + "-" + s4() + "-" + s4() + s4() + s4()).toUpperCase();
			},
			goResult() {
				uni.navigateTo({
					url: '/pages/results/results',
					success(res) {
						console.log(res);
					},
					fail(err) {
						// errMsg: "navigateTo:fail page "pages/index/pages/test/test" is not found"
						console.log(err);
					}
				});
			},
		}
	}
</script>

<style>
	/* #ifndef APP-PLUS */
	page {
		width: 100%;
		min-height: 100%;
		display: flex;
	}

	/* #endif */

	.tabs {
		flex: 1;
		flex-direction: column;
		overflow: hidden;
		background-color: #ffffff;
		/* #ifdef MP-ALIPAY || MP-BAIDU */
		height: 100vh;
		/* #endif */
	}

	.scroll-h {
		width: 750rpx;
		height: 80rpx;
		flex-direction: row;
		/* #ifndef APP-PLUS */
		white-space: nowrap;
		/* #endif */
		/* flex-wrap: nowrap; */
		/* border-color: #cccccc;
		border-bottom-style: solid;
		border-bottom-width: 1px; */
	}

	.line-h {
		height: 1rpx;
		background-color: #cccccc;
	}

	.uni-tab-item {
		/* #ifndef APP-PLUS */
		display: inline-block;
		/* #endif */
		flex-wrap: nowrap;
		padding-left: 34rpx;
		padding-right: 34rpx;
	}

	.uni-tab-item-title {
		color: #555;
		font-size: 30rpx;
		height: 80rpx;
		line-height: 80rpx;
		flex-wrap: nowrap;
		/* #ifndef APP-PLUS */
		white-space: nowrap;
		/* #endif */
	}

	.uni-tab-item-title-active {
		color: #007AFF;
	}

	.swiper-box {
		flex: 1;
	}

	.swiper-item {
		flex: 1;
		flex-direction: row;
	}

	.scroll-v {
		flex: 1;
		/* #ifndef MP-ALIPAY */
		flex-direction: column;
		/* #endif */
		width: 750rpx;
	}

	.update-tips {
		position: absolute;
		left: 0;
		top: 41px;
		right: 0;
		padding-top: 5px;
		padding-bottom: 5px;
		background-color: #FDDD9B;
		align-items: center;
		justify-content: center;
		text-align: center;
	}

	.update-tips-text {
		font-size: 14px;
		color: #ffffff;
	}

	.refresh {
		width: 750rpx;
		height: 64px;
		justify-content: center;
	}

	.refresh-view {
		flex-direction: row;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: center;
	}

	.refresh-icon {
		width: 30px;
		height: 30px;
		transition-duration: .5s;
		transition-property: transform;
		transform: rotate(0deg);
		transform-origin: 15px 15px;
	}

	.refresh-icon-active {
		transform: rotate(180deg);
	}

	.loading-icon {
		width: 20px;
		height: 20px;
		margin-right: 5px;
		color: #999999;
	}

	.loading-text {
		margin-left: 2px;
		font-size: 16px;
		color: #999999;
	}

	.loading-more {
		align-items: center;
		justify-content: center;
		padding-top: 10px;
		padding-bottom: 10px;
		text-align: center;
	}

	.loading-more-text {
		font-size: 28rpx;
		color: #999;
	}
</style>
